import React, { useEffect, useState } from "react";
import Navbar from "./Navbar";
import styles from "./styles.module.css";
import {
  useToast,
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalFooter,
  ModalBody,
  ModalCloseButton,
  useDisclosure,
  Button,
} from "@chakra-ui/react";
import { useDispatch, useSelector } from "react-redux";
import { getNewEmployee } from "../../redux/actions/adminActions";
import { FiArrowRightCircle } from "react-icons/fi";
import { useNavigate } from "react-router-dom";
import { AiFillCamera } from "react-icons/ai";
import Webcam from "react-webcam";

const GuestScreen = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const toast = useToast();
  const { isOpen, onOpen, onClose } = useDisclosure();

  const data = JSON.parse(localStorage.getItem("Info"));

  const [title, setTitle] = useState(data ? data.title : "");
  const [fullname, setFullName] = useState(data ? data.fullname : "");
  const [company, setCompany] = useState(data ? data.company : "");
  const [email, setEmail] = useState(data ? data.email : "");
  const [mobile, setMobile] = useState(data ? data.mobile : "");
  const [role, setRole] = useState(data ? data.role : "");
  const [group, setGroup] = useState(data ? data.group : "");
  const [count, setCount] = useState(data ? data.count : "");
  const [host, setHost] = useState(data ? data.host : "");
  const [purpose, setPurpose] = useState(data ? data.purpose : "");
  const [appointment, setAppointment] = useState(data ? data.appointment : "");
  const [photo, setPhoto] = useState(data ? data.photo : "");

  const getStaff = useSelector((state) => state.getStaff);
  const { staff } = getStaff;

  useEffect(() => {
    dispatch(getNewEmployee());
  }, [dispatch]);

  // webcam

  const videoConstraints = {
    width: 500,
    height: 500,
    facingMode: "user",
  };

  const webcamRef = React.useRef(null);

  const capture = React.useCallback(() => {
    const imageSrc = webcamRef.current.getScreenshot();
    setPhoto(imageSrc);
    toast({
      title: "Success",
      description: "Photo Captured Successfully",
      status: "success",
      duration: 9000,
      isClosable: true,
    });
    onClose();
  }, [webcamRef, toast, onClose]);

  // end of webcam

  const submitHandler = () => {
    if (
      !title ||
      !fullname ||
      !company ||
      !role ||
      !host ||
      !purpose ||
      !appointment ||
      !group
    ) {
      toast({
        title: "Warning",
        description: "All Asterixed Fields are required",
        status: "error",
        duration: 9000,
        isClosable: true,
      });
    } else {
      const findStaff = staff.filter((x) => x._id === host);
      const hostName = findStaff[0].name;
      const Info = {
        title,
        fullname,
        company,
        email,
        mobile,
        role,
        count,
        host,
        purpose,
        appointment,
        photo,
        hostName,
        group,
      };
      localStorage.setItem("Info", JSON.stringify(Info));
      navigate("/frontdesk/guest/verify");
    }
  };
  return (
    <div className={styles.application}>
      <Navbar />
      <div className={styles.contents}>
        <div className={styles.overlay}>
          <small>New Guest</small>
        </div>
        <div className={styles.formFlex}>
          <div className={styles.formFlexContainer}>
            <label>
              Title<span className={styles.danger}>*</span>
            </label>
            <input
              type="text"
              onChange={(e) => setTitle(e.target.value.replace(/[0-9]/g, ""))}
              placeholder="Title"
              value={title}
            />
          </div>
          <div className={styles.formFlexContainer}>
            <label>
              Fullname <span className={styles.danger}>*</span>
            </label>
            <input
              type="text"
              onChange={(e) =>
                setFullName(e.target.value.replace(/[0-9]/g, ""))
              }
              value={fullname}
              placeholder="Fullname"
            />
          </div>
          <div className={styles.formFlexContainer}>
            <label>
              Company<span className={styles.danger}>*</span>
            </label>
            <input
              type="text"
              onChange={(e) => setCompany(e.target.value)}
              value={company}
              placeholder="Company"
            />
          </div>
          <div className={styles.formFlexContainer}>
            <label>
              Position<span className={styles.danger}>*</span>
            </label>
            <input
              type="text"
              onChange={(e) => setRole(e.target.value.replace(/[0-9]/g, ""))}
              value={role}
              placeholder="Job Role"
            />
          </div>
          <div className={styles.formFlexContainer}>
            <label>
              Email <span>[Optional]</span>
            </label>
            <input
              type="text"
              onChange={(e) => setEmail(e.target.value)}
              value={email}
              placeholder="Email"
            />
          </div>
          <div className={styles.formFlexContainer}>
            <label>
              Mobile <span>[Optional]</span>
            </label>
            <input
              type="number"
              onChange={(e) => setMobile(e.target.value.slice(0, 14))}
              value={mobile}
              placeholder="Mobile"
            />
          </div>

          <div className={styles.formFlexContainer}>
            <label>
              Are you a group?<span className={styles.danger}>*</span>
            </label>
            <select
              type="text"
              className="form-control"
              onChange={(e) => setGroup(e.target.value)}
            >
              <option>Are you part of a group?</option>
              <option value="Yes">Yes</option>
              <option value="No">No</option>
            </select>
          </div>
          {group === "Yes" && (
            <div className={styles.formFlexContainer}>
              <label>
                How many are you in the group?
                <span className={styles.danger}>*</span>
              </label>
              <input
                type="number"
                onChange={(e) => setCount(e.target.value)}
                value={count}
                placeholder="How many are you?"
              />
            </div>
          )}

          <div className={styles.formFlexContainer}>
            <label>
              Who are you here to see?<span className={styles.danger}>*</span>
            </label>
            <select
              type="text"
              className="form-control"
              onChange={(e) => setHost(e.target.value)}
            >
              <option>Who are you here to see?</option>
              {staff &&
                staff.map((item, i) => (
                  <option value={item._id} key={i}>
                    {item.name}
                  </option>
                ))}
            </select>
          </div>

          <div className={styles.formFlexContainer}>
            <label>
              Purpose of Visit<span className={styles.danger}>*</span>
            </label>
            <select
              type="text"
              className="form-control"
              onChange={(e) => setPurpose(e.target.value)}
            >
              <option>Select Purpose of Visit</option>
              <option value="Personal">Personal</option>
              <option value="Official">Official</option>
            </select>
          </div>
          <div className={styles.formFlexContainer}>
            <label>
              Do you have an appointment<span className={styles.danger}>*</span>
            </label>
            <select
              type="text"
              className="form-control"
              onChange={(e) => setAppointment(e.target.value)}
            >
              <option>Do you have an appointment</option>
              <option value="Yes">Yes</option>
              <option value="No">No</option>
            </select>
          </div>
          {!photo ? (
            <div className={styles.formFlexContainer}>
              <label style={{ visibility: "hidden" }}>Photo</label>
              <button onClick={onOpen} className={styles.blue}>
                Take Picture <AiFillCamera />
              </button>
            </div>
          ) : (
            <div className={styles.formFlexContainer}>
              <label style={{ visibility: "hidden" }}>Next</label>
              <button
                type="button"
                onClick={submitHandler}
                className={styles.green}
              >
                Next
                <FiArrowRightCircle />
              </button>
            </div>
          )}
          {/* Modal */}
          <>
            <Modal isOpen={isOpen} onClose={onClose}>
              <ModalOverlay />
              <ModalContent>
                <ModalHeader>Guest Photo</ModalHeader>
                <ModalCloseButton />
                <ModalBody>
                  <Webcam
                    audio={false}
                    height={500}
                    ref={webcamRef}
                    screenshotFormat="image/jpeg"
                    width={500}
                    videoConstraints={videoConstraints}
                  />
                </ModalBody>

                <ModalFooter>
                  <Button variant="ghost" onClick={capture} type="button">
                    Take Picture
                  </Button>
                </ModalFooter>
              </ModalContent>
            </Modal>
          </>
          {/* Modal */}
        </div>
      </div>
    </div>
  );
};

export default GuestScreen;
