import React, { useEffect } from "react";
import Capsules from "./UI/Capsules";
import Navbar from "./Navbar";
import styles from "./styles.module.css";
import {
  CircularProgress,
  Alert,
  AlertIcon,
  Center,
  Table,
  Thead,
  Tbody,
  Tr,
  Th,
  Td,
  TableCaption,
} from "@chakra-ui/react";
import { useDispatch, useSelector } from "react-redux";
import { getlogs } from "../../redux/actions/logActions";

const CheckedOut = () => {
  const dispatch = useDispatch();

  const vmslogs = useSelector((state) => state.vmslogs);
  const { out, loading, error } = vmslogs;

  useEffect(() => {
    var date = new Date().toJSON().slice(0, 10).replace(/-/g, "/");
    dispatch(getlogs(date));

    //dispatch(getAdminDetails());
  }, [dispatch]);

  //   const vmslogs = useSelector((state) => state.vmslogs);
  //   const { logs } = vmslogs;
  return (
    <div className={styles.application}>
      <Navbar />
      <div className={styles.contents}>
        <div className={styles.overlay}>
          <small>Checkedout Guests</small>
        </div>
        {loading ? (
          <Center>
            <CircularProgress isIndeterminate color="green.300" />
          </Center>
        ) : (
          <div className={styles.guestContainer}>
            {error && (
              <Alert>
                <AlertIcon />
                {error}
              </Alert>
            )}
            {out && out.length <= 0 ? (
              <>
                <Alert status="warning">
                  <AlertIcon />
                  No Guest Available
                </Alert>
              </>
            ) : (
              <div className={styles.cardGrid}>
                <Table variant="simple">
                  <TableCaption>Checkedout Guest</TableCaption>
                  <Thead>
                    <Tr>
                      <Th>S/N</Th>
                      <Th>Title</Th>
                      <Th>Name</Th>
                      <Th>Company</Th>
                      <Th>Mobile</Th>
                      <Th>Status</Th>
                      <Th>TimeIn</Th>
                      <Th>TimeOut</Th>
                    </Tr>
                  </Thead>
                  <Tbody>
                    {out &&
                      out.map((item, index) => (
                        <Tr key={index}>
                          <Td>{index + 1}</Td>
                          <Td>{item.user.title}</Td>
                          <Td>{item.user.fullname}</Td>
                          <Td>{item.user.company}</Td>
                          <Td>{item.user.mobile}</Td>
                          <Td>{item.status}</Td>
                          <Td>{item.timeIn}</Td>
                          <Td>{item.timeOut}</Td>
                        </Tr>
                      ))}
                  </Tbody>
                </Table>
              </div>
            )}
          </div>
        )}
      </div>
    </div>
  );
};

export default CheckedOut;
