import React, { useEffect, useState, useMemo, useCallback } from "react";
import Navbar from "./Navbar";
import styles from "./styles.module.css";
import {
  useToast,
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalFooter,
  ModalBody,
  ModalCloseButton,
  useDisclosure,
  Button,
} from "@chakra-ui/react";
import { useDispatch, useSelector } from "react-redux";
import { getAllGuest, getAllRGuest } from "../../redux/actions/changeActions";
import { FiArrowRightCircle, FiDelete } from "react-icons/fi";
import { useNavigate } from "react-router-dom";
import { AiFillCamera } from "react-icons/ai";
import Webcam from "react-webcam";
import DataListInput from "react-datalist-input";
import { getNewEmployee } from "../../redux/actions/adminActions";

const ReturningGuestScreen = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const toast = useToast();
  const { isOpen, onOpen, onClose } = useDisclosure();

  const [active, setActive] = useState(false);
  const [title, setTitle] = useState("");
  const [user, setUser] = useState("");
  const [fullname, setFullName] = useState("");
  const [company, setCompany] = useState("");
  const [email, setEmail] = useState("");
  const [mobile, setMobile] = useState("");
  const [role, setRole] = useState("");
  const [group, setGroup] = useState("");
  const [count, setCount] = useState("");
  const [host, setHost] = useState("");
  const [visit, setVisit] = useState("");
  const [purpose, setPurpose] = useState("");
  const [appointment, setAppointment] = useState("");
  const [hostName, setHostName] = useState("");
  const [photo, setPhoto] = useState("");

  const getGuest = useSelector((state) => state.getGuest);
  const { guest } = getGuest;

  const getStaff = useSelector((state) => state.getStaff);
  const { staff } = getStaff;

  const getRGuest = useSelector((state) => state.getRGuest);
  const { rguest } = getRGuest;

  useEffect(() => {
    dispatch(getAllGuest());
    dispatch(getAllRGuest());
    dispatch(getNewEmployee());
  }, [dispatch]);

  // webcam

  const videoConstraints = {
    width: 500,
    height: 500,
    facingMode: "user",
  };

  const webcamRef = React.useRef(null);

  const capture = React.useCallback(() => {
    const imageSrc = webcamRef.current.getScreenshot();
    setPhoto(imageSrc);
    toast({
      title: "Success",
      description: "Photo Captured Successfully",
      status: "success",
      duration: 9000,
      isClosable: true,
    });
    onClose();
  }, [webcamRef, toast, onClose]);

  // end of webcam

  const submitHandler = () => {
    if (!group || !purpose || !host || !appointment) {
      toast({
        title: "Warning",
        description: "All Asterixed Fields are required",
        status: "error",
        duration: 9000,
        isClosable: true,
      });
    } else {
      const Info = {
        title,
        fullname,
        company,
        email,
        mobile,
        role,
        count,
        host,
        purpose,
        appointment,
        photo,
        hostName,
        group,
        user,
      };
      localStorage.setItem("Info", JSON.stringify(Info));
      navigate("/frontdesk/returningGuest/verify");
    }
  };

  const onSelect = useCallback(
    (selectedItem) => {
      setActive(true);
      setTitle(selectedItem.title);
      setFullName(selectedItem.fullname);
      setCompany(selectedItem.company);
      setEmail(selectedItem.email);
      setMobile(selectedItem.mobile);
      setRole(selectedItem.role);
      setUser(selectedItem._id);
      setHostName(selectedItem.name);
      setPhoto(selectedItem.photo);
      const findUser = rguest.filter((x) => x.user._id === selectedItem._id);
      const date = findUser[0].date;
      setVisit(date);
    },
    [rguest]
  );

  const items = useMemo(
    () =>
      guest &&
      guest.map((oneItem) => ({
        // required: what to show to the user
        label: oneItem.fullname + " - " + oneItem.company,
        // required: key to identify the item within the array
        key: oneItem._id,
        // feel free to add your own app logic to access those properties in the onSelect function
        someAdditionalValue: oneItem.company,
        // or just keep everything
        ...oneItem,
      })),
    [guest]
  );

  return (
    <div className={styles.application}>
      <Navbar />
      <div className={styles.contents}>
        <div className={styles.overlay}>
          <small>Returning Guest</small>
        </div>
        {!active ? (
          <div className={styles.formFlex}>
            <div className={styles.formFlexContainer}>
              <label>
                Full Name<span className={styles.danger}>*</span>
              </label>
              <DataListInput
                placeholder="Enter Visitor's name"
                items={items}
                onSelect={onSelect}
              />
            </div>
          </div>
        ) : (
          <div className={styles.formFlex}>
            <div className={styles.formFlexContainer}>
              <label>
                Title<span className={styles.danger}>*</span>
              </label>
              <input
                type="text"
                onChange={(e) => setTitle(e.target.value.replace(/[0-9]/g, ""))}
                placeholder="Title"
                value={title}
              />
            </div>
            <div className={styles.formFlexContainer}>
              <label>
                Fullname <span className={styles.danger}>*</span>
              </label>
              <input
                type="text"
                onChange={(e) =>
                  setFullName(e.target.value.replace(/[0-9]/g, ""))
                }
                value={fullname}
                placeholder="Fullname"
                readOnly
              />
            </div>
            <div className={styles.formFlexContainer}>
              <label>
                Company<span className={styles.danger}>*</span>
              </label>
              <input
                type="text"
                onChange={(e) => setCompany(e.target.value)}
                value={company}
                placeholder="Company"
              />
            </div>
            <div className={styles.formFlexContainer}>
              <label>
                Position<span className={styles.danger}>*</span>
              </label>
              <input
                type="text"
                onChange={(e) => setRole(e.target.value.replace(/[0-9]/g, ""))}
                value={role}
                placeholder="Job Role"
              />
            </div>
            <div className={styles.formFlexContainer}>
              <label>
                Email <span>[Optional]</span>
              </label>
              <input
                type="text"
                onChange={(e) => setEmail(e.target.value)}
                value={email}
                placeholder="Email"
              />
            </div>
            <div className={styles.formFlexContainer}>
              <label>
                Mobile <span>[Optional]</span>
              </label>
              <input
                type="number"
                onChange={(e) => setMobile(e.target.value.slice(0, 14))}
                value={mobile}
                placeholder="Mobile"
              />
            </div>
            <div className={styles.formFlexContainer}>
              <label>Last Visit</label>
              <input
                type="text"
                onChange={(e) => setVisit(e.target.value.slice(0, 14))}
                value={visit}
                readOnly
              />
            </div>

            <div className={styles.formFlexContainer}>
              <label>
                Are you part of a group?<span className={styles.danger}>*</span>
              </label>
              <select
                type="text"
                className="form-control"
                onChange={(e) => setGroup(e.target.value)}
              >
                <option>Are you part of a group?</option>
                <option value="Yes">Yes</option>
                <option value="No">No</option>
              </select>
            </div>
            {group === "Yes" && (
              <div className={styles.formFlexContainer}>
                <label>
                  How many are you in the group?
                  <span className={styles.danger}>*</span>
                </label>
                <input
                  type="number"
                  onChange={(e) => setCount(e.target.value)}
                  value={count}
                  placeholder="How many are you in the group?"
                />
              </div>
            )}

            <div className={styles.formFlexContainer}>
              <label>
                Who are you here to see?<span className={styles.danger}>*</span>
              </label>
              <select
                type="text"
                className="form-control"
                onChange={(e) => setHost(e.target.value)}
              >
                <option>Who are you here to see?</option>
                {staff &&
                  staff.map((item, i) => (
                    <option value={item._id} key={i}>
                      {item.name}
                    </option>
                  ))}
              </select>
            </div>

            <div className={styles.formFlexContainer}>
              <label>
                Purpose of Visit<span className={styles.danger}>*</span>
              </label>
              <select
                type="text"
                className="form-control"
                onChange={(e) => setPurpose(e.target.value)}
              >
                <option>Select Purpose of Visit</option>
                <option value="Personal">Personal</option>
                <option value="Official">Official</option>
              </select>
            </div>
            <div className={styles.formFlexContainer}>
              <label>
                Do you have an appointment
                <span className={styles.danger}>*</span>
              </label>
              <select
                type="text"
                className="form-control"
                onChange={(e) => setAppointment(e.target.value)}
              >
                <option>Do you have an appointment</option>
                <option value="Yes">Yes</option>
                <option value="No">No</option>
              </select>
            </div>

            <div className={styles.formFlexContainer}>
              <label>
                Photo <span>[Optional]</span>
              </label>
              <button onClick={onOpen} className={styles.blue}>
                Take Picture <AiFillCamera />
              </button>
            </div>

            <div className={styles.formFlexContainer}>
              <label style={{ visibility: "hidden" }}>Next</label>
              <button
                type="button"
                onClick={submitHandler}
                className={styles.green}
              >
                Next
                <FiArrowRightCircle />
              </button>
            </div>

            <div className={styles.formFlexContainer}>
              <label style={{ visibility: "hidden" }}>Back</label>
              <button
                type="button"
                onClick={() => setActive(!active)}
                className={styles.blue}
                style={{ background: "goldenrod" }}
              >
                Clear Records
                <FiDelete />
              </button>
            </div>

            {/* Modal */}
            <>
              <Modal isOpen={isOpen} onClose={onClose}>
                <ModalOverlay />
                <ModalContent>
                  <ModalHeader>Guest Photo</ModalHeader>
                  <ModalCloseButton />
                  <ModalBody>
                    <Webcam
                      audio={false}
                      height={500}
                      ref={webcamRef}
                      screenshotFormat="image/jpeg"
                      width={500}
                      videoConstraints={videoConstraints}
                    />
                  </ModalBody>

                  <ModalFooter>
                    <Button variant="ghost" onClick={capture} type="button">
                      Take Picture
                    </Button>
                  </ModalFooter>
                </ModalContent>
              </Modal>
            </>
            {/* Modal */}
          </div>
        )}
      </div>
    </div>
  );
};

export default ReturningGuestScreen;
