import React, { useEffect, useState } from "react";
import Navbar from "./Navbar";
import styles from "./styles.module.css";
import {
  useToast,
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalFooter,
  ModalBody,
  ModalCloseButton,
  useDisclosure,
  Button,
} from "@chakra-ui/react";
import { useDispatch, useSelector } from "react-redux";
import { getAllGuest, getAllRGuest } from "../../redux/actions/changeActions";
import { FiArrowRightCircle, FiDelete } from "react-icons/fi";
import { AiFillCamera } from "react-icons/ai";
import Webcam from "react-webcam";
import { getNewEmployee } from "../../redux/actions/adminActions";
import {
  prebookNotifyHost,
  validatePrebook,
} from "../../redux/actions/prebookActions";
import { SPREBOOK_RESET } from "../../redux/constants/guestConstants";

const VertyGuestToken = () => {
  const dispatch = useDispatch();
  const toast = useToast();
  const { isOpen, onOpen, onClose } = useDisclosure();

  const [active, setActive] = useState(false);
  const [token, setToken] = useState("");

  const [id, setId] = useState("");
  const [fullname, setFullName] = useState("");
  const [company, setCompany] = useState("");
  const [email, setEmail] = useState("");
  const [mobile, setMobile] = useState("");

  const [date, setDate] = useState("");
  const [time, setTime] = useState("");
  const [group, setGroup] = useState("");
  const [count, setCount] = useState("");
  const [purpose, setPurpose] = useState("");
  const [hostName, setHostName] = useState("");
  const [photo, setPhoto] = useState("");

  const getStaff = useSelector((state) => state.getStaff);
  const { staff } = getStaff;

  useEffect(() => {
    dispatch(getAllGuest());
    dispatch(getAllRGuest());
    dispatch(getNewEmployee());
  }, [dispatch]);

  // webcam

  const videoConstraints = {
    width: 500,
    height: 500,
    facingMode: "user",
  };

  const webcamRef = React.useRef(null);

  const capture = React.useCallback(() => {
    const imageSrc = webcamRef.current.getScreenshot();
    setPhoto(imageSrc);
    toast({
      title: "Success",
      description: "Photo Captured Successfully",
      status: "success",
      duration: 9000,
      isClosable: true,
    });
    onClose();
  }, [webcamRef, toast, onClose]);

  // end of webcam

  const submitHandler = () => {
    if (!group || !photo) {
      toast({
        title: "Warning",
        description: "All Asterixed Fields are required",
        status: "error",
        duration: 9000,
        isClosable: true,
      });
    } else {
      dispatch(prebookNotifyHost(id, group, photo));
    }
  };

  const checkHandler = () => {
    dispatch(validatePrebook(token));
  };
  const sbook = useSelector((state) => state.sbook);
  const { success, booked } = sbook;

  if (success) {
    setActive(true);
    setId(booked._id);
    setFullName(booked.fullname);
    setCompany(booked.company);
    setEmail(booked.email);
    setMobile(booked.mobile);
    setDate(booked.date);
    setTime(booked.timeIn);
    setPurpose(booked.purpose);
    const findUser = staff.filter((x) => x._id === booked.host);
    setHostName(findUser[0].name);

    dispatch({ type: SPREBOOK_RESET });
  }
  return (
    <div className={styles.application}>
      <Navbar />
      <div className={styles.contents}>
        <div className={styles.overlay}>
          <small>Returning Guest</small>
        </div>
        {!active ? (
          <div className={styles.formFlex}>
            <div className={styles.formFlexContainer}>
              <label>
                Guest Token<span className={styles.danger}>*</span>
              </label>
              <input
                type="text"
                onChange={(e) => setToken(e.target.value.slice(0, 6))}
                placeholder="Enter Guest Token"
                value={token}
              />
            </div>
            <div className={styles.formFlexContainer}>
              <label style={{ visibility: "hidden" }}>Next</label>
              <button
                type="button"
                onClick={checkHandler}
                className={styles.green}
              >
                Verify Token
                <FiArrowRightCircle />
              </button>
            </div>
          </div>
        ) : (
          <div className={styles.formFlex}>
            <div className={styles.formFlexContainer}>
              <label>Fullname</label>
              <input type="text" value={fullname} readOnly />
            </div>
            <div className={styles.formFlexContainer}>
              <label>Company</label>
              <input type="text" value={company} readOnly />
            </div>
            <div className={styles.formFlexContainer}>
              <label>Email</label>
              <input type="text" value={email} readOnly />
            </div>
            <div className={styles.formFlexContainer}>
              <label>Mobile</label>
              <input type="number" value={mobile} readOnly />
            </div>
            <div className={styles.formFlexContainer}>
              <label>Who are you here to see?</label>
              <input type="text" value={hostName} readOnly />
            </div>
            <div className={styles.formFlexContainer}>
              <label>Purpose of Visit</label>
              <input type="text" value={purpose} readOnly />
            </div>
            <div className={styles.formFlexContainer}>
              <label>Date</label>
              <input type="text" value={date} readOnly />
            </div>
            <div className={styles.formFlexContainer}>
              <label>Time</label>
              <input type="text" value={time} readOnly />
            </div>
            <div className={styles.formFlexContainer}>
              <label>
                Are you a group?<span className={styles.danger}>*</span>
              </label>
              <select
                type="text"
                className="form-control"
                onChange={(e) => setGroup(e.target.value)}
              >
                <option>Are you part of a group?</option>
                <option value="Yes">Yes</option>
                <option value="No">No</option>
              </select>
            </div>
            {group === "Yes" && (
              <div className={styles.formFlexContainer}>
                <label>
                  How many people are in the group?
                  <span className={styles.danger}>*</span>
                </label>
                <input
                  type="number"
                  onChange={(e) => setCount(e.target.value)}
                  value={count}
                  placeholder="How many people are in the group?"
                />
              </div>
            )}

            <div className={styles.formFlexContainer}>
              <label>
                Photo <span className={styles.danger}>*</span>
              </label>
              <button onClick={onOpen} className={styles.blue}>
                Take Picture <AiFillCamera />
              </button>
            </div>

            <div className={styles.formFlexContainer}>
              <label style={{ visibility: "hidden" }}>Next</label>
              <button
                type="button"
                onClick={submitHandler}
                className={styles.green}
              >
                Notify Host
                <FiArrowRightCircle />
              </button>
            </div>

            <div className={styles.formFlexContainer}>
              <label style={{ visibility: "hidden" }}>Back</label>
              <button
                type="button"
                onClick={() => setActive(!active)}
                className={styles.blue}
                style={{ background: "goldenrod" }}
              >
                Clear Records
                <FiDelete />
              </button>
            </div>

            {/* Modal */}
            <>
              <Modal isOpen={isOpen} onClose={onClose}>
                <ModalOverlay />
                <ModalContent>
                  <ModalHeader>Guest Photo</ModalHeader>
                  <ModalCloseButton />
                  <ModalBody>
                    <Webcam
                      audio={false}
                      height={500}
                      ref={webcamRef}
                      screenshotFormat="image/jpeg"
                      width={500}
                      videoConstraints={videoConstraints}
                    />
                  </ModalBody>

                  <ModalFooter>
                    <Button variant="ghost" onClick={capture} type="button">
                      Take Picture
                    </Button>
                  </ModalFooter>
                </ModalContent>
              </Modal>
            </>
            {/* Modal */}
          </div>
        )}
      </div>
    </div>
  );
};

export default VertyGuestToken;
