import React, { useEffect } from "react";
import Navbar from "./Navbar";
import styles from "./styles.module.css";
import {
  CircularProgress,
  Alert,
  AlertIcon,
  Center,
  Table,
  Thead,
  Tbody,
  Tr,
  Th,
  Td,
  TableCaption,
  useToast,
} from "@chakra-ui/react";
import { useDispatch, useSelector } from "react-redux";
import { getlogs } from "../../redux/actions/logActions";
import { signoutUser } from "../../redux/actions/guestActions";
import { SIGNOUT_RESET } from "../../redux/constants/guestConstants";

const Checkedin = () => {
  const dispatch = useDispatch();
  const toast = useToast();

  const vmslogs = useSelector((state) => state.vmslogs);
  const { vin, loading, error } = vmslogs;

  useEffect(() => {
    var date = new Date().toJSON().slice(0, 10).replace(/-/g, "/");
    dispatch(getlogs(date));

    //dispatch(getAdminDetails());
  }, [dispatch]);
  var date = new Date().toJSON().slice(0, 10).replace(/-/g, "/");
  const signout = (tag) => {
    dispatch(signoutUser(tag));
  };
  const logout = useSelector((state) => state.logout);
  const { success, loading: sLoading } = logout;

  if (success) {
    toast({
      title: "Notification",
      description: "Signout Successfull",
      status: "success",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: SIGNOUT_RESET });
    dispatch(getlogs(date));
  }
  return (
    <div className={styles.application}>
      <Navbar />
      <div className={styles.contents}>
        <div className={styles.overlay}>
          <small>Checkedin Guests</small>
        </div>
        {loading ? (
          <Center>
            <CircularProgress isIndeterminate color="green.300" />
          </Center>
        ) : (
          <div className={styles.guestContainer}>
            {error && (
              <Alert>
                <AlertIcon />
                {error}
              </Alert>
            )}
            {vin && vin.length <= 0 ? (
              <>
                <Alert status="warning">
                  <AlertIcon />
                  No Guest Available
                </Alert>
              </>
            ) : (
              <div>
                <Table variant="simple">
                  <TableCaption>Checkedin Guest</TableCaption>
                  <Thead>
                    <Tr>
                      <Th>S/N</Th>
                      <Th>Title</Th>
                      <Th>Name</Th>
                      <Th>Company</Th>
                      <Th>Mobile</Th>
                      <Th>Status</Th>
                      <Th>TimeIn</Th>
                      <Th>Actions</Th>
                    </Tr>
                  </Thead>
                  <Tbody>
                    {vin &&
                      vin.map((item, index) => (
                        <Tr key={index}>
                          <Td>{index + 1}</Td>
                          <Td>{item.user.title}</Td>
                          <Td>{item.user.fullname}</Td>
                          <Td>{item.user.company}</Td>
                          <Td>{item.user.mobile}</Td>
                          <Td>{item.status}</Td>
                          <Td>{item.timeIn}</Td>
                          <Td>
                            {sLoading ? (
                              <></>
                            ) : (
                              <button
                                type="button"
                                onClick={() => signout(item.tag)}
                              >
                                Sign out
                              </button>
                            )}
                          </Td>
                        </Tr>
                      ))}
                  </Tbody>
                </Table>
              </div>
            )}
          </div>
        )}
      </div>
    </div>
  );
};

export default Checkedin;
