import {
  ADD_GUEST_FAIL,
  ADD_GUEST_REQUEST,
  ADD_GUEST_RESET,
  ADD_GUEST_SUCCESS,
  GET_GUEST_FAIL,
  GET_GUEST_REQUEST,
  GET_GUEST_RESET,
  GET_GUEST_SUCCESS,
  GET_RGUEST_FAIL,
  GET_RGUEST_REQUEST,
  GET_RGUEST_RESET,
  GET_RGUEST_SUCCESS,
  ADD_RGUEST_FAIL,
  ADD_RGUEST_REQUEST,
  ADD_RGUEST_RESET,
  ADD_RGUEST_SUCCESS,
} from "../constants/changeConstants";

export const changeReducer = (state = {}, action) => {
  switch (action.type) {
    case ADD_GUEST_REQUEST:
      return { loading: true };
    case ADD_GUEST_SUCCESS:
      return { loading: false, success: true };
    case ADD_GUEST_FAIL:
      return { loading: false, error: action.payload };
    case ADD_GUEST_RESET:
      return {};
    default:
      return state;
  }
};

export const changeRGReducer = (state = {}, action) => {
  switch (action.type) {
    case ADD_RGUEST_REQUEST:
      return { loading: true };
    case ADD_RGUEST_SUCCESS:
      return { loading: false, success: true };
    case ADD_RGUEST_FAIL:
      return { loading: false, error: action.payload };
    case ADD_RGUEST_RESET:
      return {};
    default:
      return state;
  }
};

export const getAllGuestReducer = (state = { guest: [] }, action) => {
  switch (action.type) {
    case GET_GUEST_REQUEST:
      return { ...state, loading: true };
    case GET_GUEST_SUCCESS:
      return { loading: false, success: true, guest: action.payload.data };
    case GET_GUEST_FAIL:
      return { loading: false, error: action.payload };
    case GET_GUEST_RESET:
      return {};
    default:
      return state;
  }
};

export const getAllRGuestReducer = (state = { rguest: [] }, action) => {
  switch (action.type) {
    case GET_RGUEST_REQUEST:
      return { ...state, loading: true };
    case GET_RGUEST_SUCCESS:
      return { loading: false, success: true, rguest: action.payload.data };
    case GET_RGUEST_FAIL:
      return { loading: false, error: action.payload };
    case GET_RGUEST_RESET:
      return {};
    default:
      return state;
  }
};
