import {
  P_NOTIFY_REQUEST,
  P_NOTIFY_FAIL,
  P_NOTIFY_SUCCESS,
  SPREBOOK_SUCCESS,
  SPREBOOK_REQUEST,
  SPREBOOK_FAIL,
} from "../constants/prebookConstants";
import axios from "axios";

export const prebookNotifyHost =
  (id, group, photo) => async (dispatch, getState) => {
    try {
      dispatch({ type: P_NOTIFY_REQUEST });

      const { data } = await axios.post(`/api/v1/prebook/notify`, {
        id,
        group,
        photo,
      });

      dispatch({
        type: P_NOTIFY_SUCCESS,
        payload: data,
      });
    } catch (error) {
      dispatch({
        type: P_NOTIFY_FAIL,
        payload:
          error.response && error.response.data.error
            ? error.response.data.error
            : error.message,
      });
    }
  };

export const validatePrebook = (token) => async (dispatch, getState) => {
  try {
    dispatch({ type: SPREBOOK_REQUEST });

    const { data } = await axios.post(`/api/v1/prebook/checkin`, { token });

    dispatch({
      type: SPREBOOK_SUCCESS,
      payload: data,
    });
  } catch (error) {
    dispatch({
      type: SPREBOOK_FAIL,
      payload:
        error.response && error.response.data.error
          ? error.response.data.error
          : error.message,
    });
  }
};
