import React, { useState, useEffect, useCallback, useMemo } from "react";
import Navbar from "./Navbar";
import styles from "./styles.module.css";
import {
  CircularProgress,
  Alert,
  AlertIcon,
  Center,
  useToast,
} from "@chakra-ui/react";
import { useDispatch, useSelector } from "react-redux";
import { prebookNewGuest } from "../../redux/actions/guestActions";
import { PREBOOK_RESET } from "../../redux/constants/guestConstants";
import { getNewEmployee } from "../../redux/actions/adminActions";
import DataListInput from "react-datalist-input";

const PreebookGuest = () => {
  const toast = useToast();
  const [fullname, setFullName] = useState("");
  const [company, setCompany] = useState("");
  const [email, setEmail] = useState("");
  const [mobile, setMobile] = useState("");
  const [host, setHost] = useState("");
  const [timeIn, setTime] = useState("");
  const [date, setDate] = useState("");
  const [purpose, setPurpose] = useState("");

  const dispatch = useDispatch();

  const book = useSelector((state) => state.book);
  const { loading, error, success } = book;

  const submitHandler = (e) => {
    e.preventDefault();
    if (
      !fullname ||
      !company ||
      !email ||
      !mobile ||
      !host ||
      !timeIn ||
      !date ||
      !purpose
    ) {
      toast({
        title: "Error",
        description: "All Fields are required",
        status: "error",
        duration: 9000,
        isClosable: true,
      });
    } else {
      dispatch(
        prebookNewGuest(
          fullname,
          company,
          email,
          mobile,
          host,
          timeIn,
          date,
          purpose
        )
      );
    }
  };
  if (success) {
    toast({
      title: "Notification",
      description: "Guest has been prebooked",
      status: "success",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: PREBOOK_RESET });
  }
  const getStaff = useSelector((state) => state.getStaff);
  const { staff } = getStaff;

  useEffect(() => {
    dispatch(getNewEmployee());
  }, [dispatch]);

  const disablePastDate = () => {
    const today = new Date();
    const dd = String(today.getDate() + 1).padStart(2, "0");
    const mm = String(today.getMonth() + 1).padStart(2, "0"); //January is 0!
    const yyyy = today.getFullYear();
    return yyyy + "-" + mm + "-" + dd;
  };
  const onSelect = useCallback((selectedItem) => {
    setHost(selectedItem._id);
  }, []);

  const items = useMemo(
    () =>
      staff &&
      staff.map((oneItem) => ({
        // required: what to show to the user
        label: oneItem.name,
        // required: key to identify the item within the array
        key: oneItem._id,
        // feel free to add your own app logic to access those properties in the onSelect function
        someAdditionalValue: oneItem.email,
        // or just keep everything
        ...oneItem,
      })),
    [staff]
  );
  return (
    <div className={styles.application}>
      <Navbar />
      <div className={styles.contents}>
        <div className={styles.overlay}>
          <small>Pre-Book a Guest</small>
        </div>
        {loading ? (
          <Center>
            <CircularProgress isIndeterminate color="green.300" />
          </Center>
        ) : (
          <div>
            {error && (
              <Alert>
                <AlertIcon />
                {error}
              </Alert>
            )}
            <form onSubmit={submitHandler}>
              <div className={styles.formFlex}>
                <div className={styles.formFlexContainer}>
                  <label>
                    Name <span className={styles.danger}>*</span>
                  </label>
                  <input
                    type="text"
                    onChange={(e) =>
                      setFullName(e.target.value.replace(/[0-9]/g, ""))
                    }
                    className="form-control"
                  />
                </div>
                <div className={styles.formFlexContainer}>
                  <label>
                    Company <span className={styles.danger}>*</span>
                  </label>
                  <input
                    type="text"
                    onChange={(e) => setCompany(e.target.value)}
                    className="form-control"
                  />
                </div>
                <div className={styles.formFlexContainer}>
                  <label>
                    Email <span className={styles.danger}>*</span>
                  </label>
                  <input
                    type="email"
                    onChange={(e) => setEmail(e.target.value)}
                    className="form-control"
                  />
                </div>
                <div className={styles.formFlexContainer}>
                  <label>
                    Mobile <span className={styles.danger}>*</span>
                  </label>
                  <input
                    type="number"
                    onChange={(e) => setMobile(e.target.value.slice(0, 14))}
                    className="form-control"
                  />
                </div>
                <div className={styles.formFlexContainer}>
                  <label>
                    Host <span className={styles.danger}>*</span>
                  </label>
                  <DataListInput
                    placeholder="Enter Host"
                    items={items}
                    onSelect={onSelect}
                  />
                </div>
                <div className={styles.formFlexContainer}>
                  <label>
                    Purpose of Visit<span className={styles.danger}>*</span>
                  </label>
                  <select
                    type="text"
                    className="form-control"
                    onChange={(e) => setPurpose(e.target.value)}
                  >
                    <option>Select Purpose of Visit</option>
                    <option value="Personal">Personal</option>
                    <option value="Official">Official</option>
                  </select>
                </div>
                <div className={styles.formFlexContainer}>
                  <label>
                    Date <span className={styles.danger}>*</span>
                  </label>
                  <input
                    type="date"
                    onChange={(e) => setDate(e.target.value)}
                    className="form-control"
                    min={disablePastDate()}
                  />
                </div>
                <div className={styles.formFlexContainer}>
                  <label>
                    Time <span className={styles.danger}>*</span>
                  </label>
                  <input
                    type="time"
                    onChange={(e) => setTime(e.target.value)}
                    className="form-control"
                  />
                </div>
                <div className={styles.formFlexContainer}>
                  <label style={{ visibility: "hidden" }}>Submit</label>
                  <input
                    type="submit"
                    className={`${styles.darkBlue}`}
                    value="Prebook Guest"
                  />
                </div>
              </div>
            </form>
          </div>
        )}
      </div>
    </div>
  );
};

export default PreebookGuest;
