import axios from "axios";
import {
  ADD_GUEST_SUCCESS,
  ADD_GUEST_REQUEST,
  ADD_GUEST_FAIL,
  GET_GUEST_FAIL,
  GET_GUEST_REQUEST,
  GET_GUEST_SUCCESS,
  GET_RGUEST_FAIL,
  GET_RGUEST_REQUEST,
  GET_RGUEST_SUCCESS,
  ADD_RGUEST_SUCCESS,
  ADD_RGUEST_REQUEST,
  ADD_RGUEST_FAIL,
} from "../constants/changeConstants";

export const changeGuest =
  (
    fullname,
    company,
    email,
    mobile,
    host,
    purpose,
    appointment,
    photo,
    timeIn,
    date,
    group,
    count
  ) =>
  async (dispatch) => {
    try {
      dispatch({ type: ADD_GUEST_REQUEST });

      const config = {
        headers: {
          "Content-Type": "application/json",
          "Access-Control-Allow-Origin": "*",
        },
      };
      const { data } = await axios.post(
        `/api/v1/guestMost`,
        {
          fullname,
          company,
          email,
          mobile,
          host,
          purpose,
          appointment,
          photo,
          timeIn,
          date,
          group,
          count,
        },
        config
      );

      dispatch({
        type: ADD_GUEST_SUCCESS,
        payload: data,
      });
    } catch (error) {
      dispatch({
        type: ADD_GUEST_FAIL,
        payload:
          error.response && error.response.data.error
            ? error.response.data.error
            : error.message,
      });
    }
  };

export const getAllGuest = () => async (dispatch) => {
  try {
    dispatch({ type: GET_GUEST_REQUEST });

    const config = {
      headers: {
        "Content-Type": "application/json",
        "Access-Control-Allow-Origin": "*",
      },
    };
    const { data } = await axios.get(
      `/api/v1/guestMost`,

      config
    );

    dispatch({
      type: GET_GUEST_SUCCESS,
      payload: data,
    });
  } catch (error) {
    dispatch({
      type: GET_GUEST_FAIL,
      payload:
        error.response && error.response.data.error
          ? error.response.data.error
          : error.message,
    });
  }
};

