import React, { useEffect, useState } from "react";
import Navbar from "./Navbar";
import styles from "./styles.module.css";
import { useDispatch, useSelector } from "react-redux";
import { getStaffDetails } from "../../redux/actions/staffActions";

const StaffProfile = () => {
  const dispatch = useDispatch();

  const staffProfile = useSelector((state) => state.staffProfile);
  const { staff } = staffProfile;

  useEffect(() => {
    dispatch(getStaffDetails());
  }, [dispatch]);

  const [name, setName] = useState(staff && staff.name);
  const [email, setEmail] = useState(staff && staff.email);
  const [mobile, setMobile] = useState(staff && staff.mobile);
  const [phone, setPhone] = useState(staff && staff.phone);
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");

  //   const vmslogs = useSelector((state) => state.vmslogs);
  //   const { logs } = vmslogs;
  return (
    <div className={styles.application}>
      <Navbar />
      <div className={styles.contents}>
        <div className={styles.overlay}>
          <div className={styles.title}>
            <small>Profile</small>
          </div>
        </div>
        <div className={styles.pageFlex}>
          <div className={styles.formFlexContainer}>
            <label>
              Name
              <span className={styles.danger}>*</span>
            </label>
            <input
              type="text"
              onChange={(e) => setName(e.target.value)}
              value={name}
            />
          </div>
          <div className={styles.formFlexContainer}>
            <label>
              Email
              <span className={styles.danger}>*</span>
            </label>
            <input
              type="text"
              onChange={(e) => setEmail(e.target.value)}
              value={email}
            />
          </div>
          <div className={styles.formFlexContainer}>
            <label>
              Primary Contact
              <span className={styles.danger}>*</span>
            </label>
            <input
              type="text"
              onChange={(e) => setMobile(e.target.value)}
              value={mobile}
            />
          </div>
          <div className={styles.formFlexContainer}>
            <label>
              Secondary Contact
              <span className={styles.danger}>*</span>
            </label>
            <input
              type="text"
              onChange={(e) => setPhone(e.target.value)}
              value={phone}
            />
          </div>
          <div className={styles.formFlexContainer}>
            <label>
              New Password
              <span className={styles.danger}>*</span>
            </label>
            <input
              type="password"
              onChange={(e) => setPassword(e.target.value)}
              value={password}
            />
          </div>
          <div className={styles.formFlexContainer}>
            <label>
              Confirm Password
              <span className={styles.danger}>*</span>
            </label>
            <input
              type="password"
              onChange={(e) => setConfirmPassword(e.target.value)}
              value={confirmPassword}
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default StaffProfile;
