import React from "react";
import Navbar from "./Navbar";
import styles from "./styles.module.css";
import { Center, CircularProgress, useToast } from "@chakra-ui/react";
import { useDispatch, useSelector } from "react-redux";
import { FiArrowRightCircle, FiArrowLeftCircle } from "react-icons/fi";

import { useNavigate, Link } from "react-router-dom";
import { ADD_GUEST_RESET } from "../../redux/constants/changeConstants";
import { changeGuest } from "../../redux/actions/changeActions";

const Review = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const toast = useToast();

  const Info = JSON.parse(localStorage.getItem("Info"));
  const {
    title,
    fullname,
    company,
    email,
    mobile,
    role,
    count,
    host,
    purpose,
    appointment,
    photo,
    group,
    hostName,
  } = Info;

  var timeIn = new Date().toLocaleString();
  var date = new Date().toJSON().slice(0, 10).replace(/-/g, "/");
  const submitHandler = () => {
    dispatch(
      changeGuest(
        fullname,
        company,
        email,
        mobile,
        host,
        purpose,
        appointment,
        photo,
        timeIn,
        date,
        group,
        count
      )
    );
  };
  const addGuest = useSelector((state) => state.addGuest);
  const { loading, error, success } = addGuest;
  if (success) {
    toast({
      title: "Success",
      description: "Host has been Notified",
      status: "success",
      duration: 9000,
      isClosable: true,
    });

    navigate("/frontdesk/dashboard");
    dispatch({ type: ADD_GUEST_RESET });
  }

  if (error) {
    toast({
      title: "Warning!",
      description: error,
      status: "error",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: ADD_GUEST_RESET });
  }

  return (
    <div className={styles.application}>
      <Navbar />
      <div className={styles.contents}>
        <div className={styles.overlay}>
          <small>New Guest</small>
        </div>
        {loading ? (
          <Center>
            <CircularProgress isIndeterminate color="green.500" />
          </Center>
        ) : (
          <>
            <div className={`${styles.formFlex}`}>
              <div className={styles.photo}>
                <img src={photo} alt={fullname} />
              </div>
              <div className={styles.reviewFlex}>
                <div className={styles.formFlexContainer}>
                  <label>Name</label>
                  <p>{title + " " + fullname}</p>
                </div>
                <div className={styles.formFlexContainer}>
                  <label>Company</label>
                  <p>{company}</p>
                </div>
                {email && (
                  <div className={styles.formFlexContainer}>
                    <label>Email</label>
                    <p>{email}</p>
                  </div>
                )}
                {mobile && (
                  <div className={styles.formFlexContainer}>
                    <label>Mobile</label>
                    <p>{mobile}</p>
                  </div>
                )}

                <div className={styles.formFlexContainer}>
                  <label>Job Role</label>
                  <p>{role}</p>
                </div>
                {group && (
                  <div className={styles.formFlexContainer}>
                    <label>Are you a group?</label>
                    <p>{group}</p>
                  </div>
                )}
                {count && (
                  <div className={styles.formFlexContainer}>
                    <label>How many are you in the group?</label>
                    <p>{count}</p>
                  </div>
                )}
                <div className={styles.formFlexContainer}>
                  <label>Who are you here to see?</label>
                  <p>{hostName}</p>
                </div>
                <div className={styles.formFlexContainer}>
                  <label>Purpose of Visit</label>
                  <p>{purpose}</p>
                </div>
                <div className={styles.formFlexContainer}>
                  <label>Do you have an appointment?</label>
                  <p>{appointment}</p>
                </div>
                {/* Buttons */}

                <div className={styles.actions}>
                  <div className={styles.reviewFlexContainer}>
                    <Link
                      to="/frontdesk/guest"
                      className={`${styles.iBtn} ${styles.blue}`}
                      style={{
                        color: "white",
                        // marginRight: "2rem",
                        // marginLeft: "2rem",
                      }}
                    >
                      Go Back
                      <FiArrowLeftCircle />
                    </Link>
                  </div>
                  <div className={styles.reviewFlexContainer}>
                    <button
                      type="button"
                      onClick={submitHandler}
                      className={styles.green}
                      style={{
                        height: "4rem",
                        padding: "1rem 2rem",
                      }}
                    >
                      Submit
                      <FiArrowRightCircle />
                    </button>
                  </div>
                </div>

                {/* Buttons */}
              </div>
            </div>
          </>
        )}
      </div>
    </div>
  );
};

export default Review;
