import React, { useEffect } from "react";
import Navbar from "./Navbar";
import styles from "./styles.module.css";
import { CircularProgress, Alert, AlertIcon, Center } from "@chakra-ui/react";
import { useDispatch, useSelector } from "react-redux";
import { getlogs } from "../../redux/actions/logActions";
// import ReactHTMLTableToExcel from "react-html-table-to-excel";
import MaterialTable from "material-table";

const Logs = () => {
  const dispatch = useDispatch();

  const vmslogs = useSelector((state) => state.vmslogs);
  const { allLogs, loading, error } = vmslogs;

  useEffect(() => {
    var date = new Date().toJSON().slice(0, 10).replace(/-/g, "/");
    dispatch(getlogs(date));

    //dispatch(getAdminDetails());
  }, [dispatch]);

  //   const vmslogs = useSelector((state) => state.vmslogs);
  //   const { logs } = vmslogs;

  return (
    <div className={styles.application}>
      <Navbar />
      <div className={styles.contents}>
        <div className={styles.overlay}>
          <small>All Logs</small>
        </div>
        {loading ? (
          <Center>
            <CircularProgress isIndeterminate color="green.300" />
          </Center>
        ) : (
          <div className={styles.guestContainer}>
            {error && (
              <Alert>
                <AlertIcon />
                {error}
              </Alert>
            )}
            {allLogs && allLogs.length <= 0 ? (
              <>
                <Alert status="warning">
                  <AlertIcon />
                  No Guest Available
                </Alert>
              </>
            ) : (
              <>
                {/* <Button colorScheme="teal">
                  <ReactHTMLTableToExcel
                    id="test-table-xls-button"
                    className="download-table-xls-button"
                    table="table-to-xls"
                    filename="Visitors Log"
                    sheet="tablexls"
                    buttonText="Download as XLS"
                  />
                </Button> */}
                <MaterialTable
                  columns={[
                    { title: "Name", field: "user.fullname" },
                    { title: "Company", field: "user.company" },
                    { title: "Email", field: "user.email" },
                    { title: "Mobile", field: "user.mobile" },
                    { title: "Date", field: "date" },
                    { title: "Status", field: "status" },
                    { title: "TimeIn", field: "timeIn" },
                    { title: "TimeOut", field: "timeOut" },
                  ]}
                  data={allLogs && allLogs}
                  title="Visitors Log"
                  options={{
                    exportButton: true,
                    headerStyle: {
                      backgroundColor: "#01579b",
                      color: "#FFF",
                    },
                  }}
                  style={{ boxShadow: "none" }}
                />
              </>
            )}
          </div>
        )}
      </div>
    </div>
  );
};

export default Logs;
